<template>
  <div class="row justify-content-center">
    <div class="col-md-8">
      <div class="card">
        <div class="card-header">Reset Password</div>
        <div class="card-body">
          <div class="alert alert-info" role="alert" v-if="'message' in notify">
            {{ notify.message }}
          </div>
          <alert-error :form="form"></alert-error>
          <alert-success :form="form" :message="message"></alert-success>
          <form
            autocomplete="off"
            @submit.prevent="onReset"
            @keydown="form.onKeydown($event)"
          >
            <div class="form-group row">
              <label for="token" class="col-md-4 col-form-label text-md-right"
                >OTP</label
              >
              <div class="col-md-6">
                <input
                  type="tel"
                  id="token"
                  name="token"
                  autofocus="autofocus"
                  required="required"
                  v-model.number="form.token"
                  :class="[
                    'form-control',
                    form.errors.has('token') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="token"></has-error>
              </div>
            </div>
            <div class="form-group row">
              <label
                for="password"
                class="col-md-4 col-form-label text-md-right"
                >Password</label
              >
              <div class="col-md-6">
                <input
                  type="password"
                  id="password"
                  name="password"
                  autocomplete="new-password"
                  required="required"
                  v-model="form.password"
                  :class="[
                    'form-control',
                    form.errors.has('password') ? 'is-invalid' : ''
                  ]"
                  data-initial-value
                />
                <has-error :form="form" field="password"></has-error>
              </div>
            </div>
            <div class="form-group row">
              <label
                for="password-confirmation"
                class="col-md-4 col-form-label text-md-right"
                >Confirm Password</label
              >
              <div class="col-md-6">
                <input
                  type="password"
                  id="password-confirmation"
                  name="password_confirmation"
                  autocomplete="new-password"
                  required="required"
                  v-model="form.password_confirmation"
                  :class="[
                    'form-control',
                    form.errors.has('password_confirmation') ? 'is-invalid' : ''
                  ]"
                />
                <has-error
                  :form="form"
                  field="password_confirmation"
                ></has-error>
              </div>
            </div>
            <div class="form-group row mb-0">
              <div class="col-md-6 offset-md-4">
                <button
                  type="submit"
                  class="btn btn-success"
                  :disabled="form.errors.hasAny() || form.busy"
                >
                  <span v-if="form.busy">
                    <span class="spinner-grow spinner-grow-sm"></span>
                    Reseting Password...
                  </span>
                  <span v-else>Reset Password</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Form } from "@/lib/form";
export default {
  name: "Reset",
  data() {
    return {
      form: new Form({
        token: null,
        password: "",
        password_confirmation: ""
      }),
      message: ""
    };
  },
  computed: {
    notify() {
      return this.$store.getters.notify;
    }
  },
  methods: {
    onReset() {
      this.form
        .submit("post", "password/reset")
        .then(response => {
          this.$store.commit("AUTH_NOTIFY", []);
          this.message = response.data.message;
          setTimeout(() => {
            this.$router.push({ name: "Login" });
          }, 3000);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style></style>
