<template>
  <div class="row justify-content-center">
    <div class="col-md-8">
      <div class="card">
        <div class="card-header">Verify Mobile</div>
        <div class="card-body">
          <div class="alert alert-info" role="alert" v-if="'message' in notify">
            {{ notify.message }}
          </div>
          <alert-error :form="form"></alert-error>
          <alert-success :form="form" :message="message"></alert-success>
          <form
            autocomplete="off"
            @submit.prevent="onVerify"
            @keydown="form.onKeydown($event)"
          >
            <div class="form-group row">
              <label for="code" class="col-md-4 col-form-label text-md-right"
                >OTP</label
              >
              <div class="col-md-6">
                <input
                  type="tel"
                  id="code"
                  name="code"
                  autofocus="autofocus"
                  required="required"
                  v-model="form.code"
                  :class="[
                    'form-control',
                    form.errors.has('code') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="code"></has-error>
              </div>
            </div>
            <div class="form-group row mb-0">
              <div class="col-md-6 offset-md-4">
                <button
                  type="submit"
                  class="btn btn-success"
                  :disabled="form.errors.has('code') || form.busy"
                >
                  <span v-if="form.busy">
                    <span class="spinner-grow spinner-grow-sm"></span>
                    Verifing...
                  </span>
                  <span v-else>Verify</span>
                </button>
                <button class="btn" disabled v-if="countDown !== '00:00'">
                  Kindly wait for {{ countDown }}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Form } from "@/lib/form";
export default {
  name: "Verify",
  data() {
    return {
      form: new Form({
        mobile: null,
        code: null
      }),
      message: "",
      countDown: "00:00",
      resend: false,
      seconds: 119
    };
  },
  computed: {
    notify() {
      return this.$store.getters.notify;
    }
  },
  methods: {
    onVerify() {
      this.form
        .submit("post", "sms/verify")
        .then(response => {
          this.$store.commit("AUTH_NOTIFY", []);
          this.message = response.data.message;
          setTimeout(() => {
            this.$router.push({ name: "Login" });
          }, 3000);
        })
        .catch(error => {
          console.log(error);
        });
    },
    timer(time) {
      var min = Math.floor(time / 60);
      var sec = time - min * 60;
      min = min < 10 ? "0" + min : min;
      sec = sec < 10 ? "0" + sec : sec;
      this.countDown = min + ":" + sec;
      setTimeout(() => {
        if (time === 0) {
          this.resend = true;
          return false;
        } else {
          this.timer(time - 1);
        }
      }, 1000);
    }
  },
  mounted() {
    this.timer(this.seconds);
  }
};
</script>

<style></style>
