<template>
  <div v-if="form.successful" class="alert alert-success" role="alert">
    <slot>
      <div v-html="message" />
    </slot>
  </div>
</template>

<script>
export default {
  name: "AlertSuccess",
  props: {
    form: {
      type: Object,
      required: true
    },
    message: { type: String, default: "" }
  }
};
</script>
