<template>
  <div class="row justify-content-center">
    <div class="col-md-8">
      <div class="card">
        <div class="card-header">Reset Password</div>
        <div class="card-body">
          <alert-error :form="form"></alert-error>
          <form
            autocomplete="off"
            @submit.prevent="onForgot"
            @keydown="form.onKeydown($event)"
          >
            <div class="form-group row">
              <label for="mobile" class="col-md-4 col-form-label text-md-right"
                >Mobile</label
              >
              <div class="col-md-6">
                <input
                  type="tel"
                  id="mobile"
                  name="mobile"
                  autofocus="autofocus"
                  required="required"
                  v-model.number="form.mobile"
                  :class="[
                    'form-control',
                    form.errors.has('mobile') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="mobile"></has-error>
              </div>
            </div>
            <div class="form-group row mb-0">
              <div class="col-md-6 offset-md-4">
                <button
                  type="submit"
                  class="btn btn-success"
                  :disabled="form.errors.has('mobile') || form.busy"
                >
                  <span v-if="form.busy">
                    <span class="spinner-grow spinner-grow-sm"></span>
                    Sending...
                  </span>
                  <span v-else>Send</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Form } from "@/lib/form";
export default {
  name: "Forgot",
  data() {
    return {
      form: new Form({
        mobile: null
      })
    };
  },
  methods: {
    onForgot() {
      this.form
        .submit("post", "password/sms")
        .then(response => {
          const payload = {
            message: response.data.message,
            mobile: this.form.mobile
          };
          this.$store.commit("AUTH_NOTIFY", payload);
          this.$router.push({ name: "Reset" });
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style></style>
