import Vue from "vue";

export default {
  state: {
    cart: JSON.parse(localStorage.getItem("cart")) || []
  },
  mutations: {
    ADD_TO_CART(state, item) {
      let itemInCart = state.cart.find(product => product.slug === item.slug);
      if (itemInCart) {
        itemInCart.quantity++;
        itemInCart.totalPrice =
          (itemInCart.price - itemInCart.discountPrice) * itemInCart.quantity;
      } else {
        state.cart.push(item);
        Vue.set(item, "quantity", 1);
        Vue.set(item, "totalPrice", item.price - item.discountPrice);
      }
      this.commit("SAVE_CART");
    },
    UPDATE_ON_CART(state, { item, qty }) {
      let itemInCart = state.cart.find(product => product.slug === item.slug);
      if (itemInCart) itemInCart.quantity = qty;
    },
    REMOVE_FROM_CART(state, item) {
      const index = state.cart.indexOf(item);
      if (index > -1) state.cart.splice(index, 1);
      this.commit("SAVE_CART");
    },
    SAVE_CART(state) {
      localStorage.setItem("cart", JSON.stringify(state.cart));
    },
    DROP_CART(state) {
      localStorage.removeItem("cart");
      state.cart = [];
    }
  },
  actions: {},
  getters: {
    cartProducts: state => state.cart
  }
};
