export default {
  state: {
    loading: false
  },
  mutations: {
    SHOW(state) {
      state.loading = true;
    },
    HIDE(state) {
      state.loading = false;
    }
  },
  actions: {
    showLoader({ commit }) {
      commit("SHOW");
    },
    hideLoader({ commit }) {
      commit("HIDE");
    }
  },
  getters: {
    isLoading: state => state.loading
  }
};
