import Vue from "vue";

Vue.filter("discount", function(value, discountPrice) {
  if (!value) return "";
  if (discountPrice !== "0.00")
    return (
      " " +
      parseFloat(value - discountPrice).toFixed(2) +
      '<small> <del class="text-muted">' +
      value +
      "</del> </small>"
    );
  else return " " + value;
});
