<template>
  <div v-if="form.errors.any()" class="alert alert-danger" role="alert">
    <slot>
      <div
        v-if="form.errors.has('error')"
        v-html="form.errors.get('error')"
        style="display:inline-block"
      />
      <div v-else v-html="message" style="display:inline-block" />
    </slot>
    <slot name="url"></slot>
  </div>
</template>

<script>
export default {
  name: "AlertError",
  props: {
    form: {
      type: Object,
      required: true
    },
    message: {
      type: String,
      default: "There were some problems with your input."
    }
  }
};
</script>
