import Vue from "vue";
import VueMeta from "vue-meta";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import "./lib/filters";
import axios from "axios";
import $ from "jquery";
//jquery init
window.$ = window.jQuery = $;
import "bootstrap";
import "./registerServiceWorker";
require("owl.carousel");

Vue.config.productionTip = false;

//Storage API URL
Vue.prototype.$storage = process.env.VUE_APP_STORAGE;

// Vue Meta init
Vue.use(VueMeta);

//axios init with global settings
Vue.prototype.$axios = axios;
axios.defaults.baseURL = process.env.VUE_APP_APIURL;
axios.defaults.headers.common["X-Requested-With"] = "XMLHttpRequest";
axios.defaults.headers.get["Accepts"] = "application/json";
axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";
axios.defaults.withCredentials = true;

// axios request interceptor
axios.interceptors.request.use(
  function(config) {
    store.dispatch("showLoader", true);
    return config;
  },
  function(error) {
    store.dispatch("hideLoader", false);
    return Promise.reject(error);
  }
);

// axios response interceptor
axios.interceptors.response.use(
  function(response) {
    store.dispatch("hideLoader", false);
    return response;
  },
  function(error) {
    if (
      error.response.status === 419 &&
      error.response.config.url === "register"
    ) {
      router.push({ name: "Register" });
    }

    if (
      error.response.status === 419 &&
      error.response.config.url !== "register"
    )
      router.push({ name: "Login" });
    store.dispatch("hideLoader", false);
    return Promise.reject(error);
  }
);

Vue.component("AlertSuccess", require("@/components/AlertSuccess.vue").default);
Vue.component("AlertError", require("@/components/AlertError.vue").default);
Vue.component("HasError", require("@/components/HasError.vue").default);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
