<template>
  <header class="bg-white shadow">
    <div class="container">
      <div class="row">
        <div class="col-sm-12">
          <nav class="navbar navbar-expand navbar-light flex-wrap">
            <router-link to="/" class="navbar-brand text-success">
              <i class="fas fa-pills"></i> xDrug
            </router-link>
            <div class="flex-grow-1 order-1 order-md-0 mr-md-3">
              <input-search></input-search>
            </div>
            <ul class="navbar-nav ml-auto">
              <li class="nav-item dropdown" v-if="isAuth">
                <a
                  class="nav-link dropdown-toggle"
                  href="#"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                  >Hi, Name</a
                >
                <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                  <a
                    class="dropdown-item"
                    href="#"
                    v-if="isAuth"
                    @click.prevent="logout"
                    >Logout</a
                  >
                </div>
              </li>
              <li class="nav-item" v-else>
                <router-link :to="{ name: 'Login' }" class="nav-link"
                  >Login</router-link
                >
              </li>
              <li class="nav-item" v-if="!isAuth">
                <router-link :to="{ name: 'Register' }" class="nav-link"
                  >Register</router-link
                >
              </li>
              <li class="nav-item">
                <a
                  class="nav-link"
                  href="#"
                  @click.prevent="onCart"
                  data-toggle="modal"
                  data-target="#cartModal"
                >
                  <i class="fas fa-cart-plus"></i>
                  Cart
                  <span
                    class="badge badge-danger"
                    v-if="cartProducts.length > 0"
                    >{{ cartProducts.length }}</span
                  >
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
    <!-- Checkout Modal -->
    <div
      class="modal fade"
      id="cartModal"
      tabindex="-1"
      role="dialog"
      data-backdrop="static"
      data-keyboard="false"
      v-if="showModal"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Your Cart</h5>
          </div>
          <div class="modal-body">
            <div class="alert alert-danger" v-if="!cartProducts.length">
              <i class="fas fa-shopping-cart"></i> Your cart is empty!
            </div>
            <div class="table-responsive" v-else>
              <table class="table table-hover">
                <thead>
                  <tr>
                    <th class="border-top-0">#</th>
                    <th class="border-top-0">Product</th>
                    <th class="border-top-0">Qty</th>
                    <th class="border-top-0">Price</th>
                    <th class="border-top-0">Discount</th>
                    <th class="border-top-0">Total</th>
                    <th class="border-top-0">Remove</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in cartProducts" :key="index">
                    <th scope="row">{{ index + 1 }}</th>
                    <td>{{ item.name }}</td>
                    <td>
                      <select
                        name="quantity"
                        class="form-control form-control-sm"
                        @change="onUpdate(item, $event.target.value)"
                      >
                        <option
                          v-for="n in 7"
                          :key="n"
                          :value="n"
                          :selected="item.quantity === n"
                          >{{ n }}</option
                        >
                      </select>
                    </td>
                    <td style="white-space: nowrap;"> {{ item.price }}</td>
                    <td style="white-space: nowrap;">
                       {{ item.discountPrice }}
                    </td>
                    <td style="white-space: nowrap;">
                       {{ item.totalPrice }}
                    </td>
                    <td>
                      <button
                        class="btn btn-sm btn-danger"
                        type="button"
                        @click="onRemove(item)"
                      >
                        <i class="fas fa-trash-alt text-white"></i>
                      </button>
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>
                      <strong>Subtotal:</strong>
                    </td>
                    <td>
                      <strong style="white-space: nowrap;"
                        > {{ total }}</strong
                      >
                    </td>
                    <td></td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
              @click="onClose"
            >
              Close
            </button>
            <button
              type="button"
              class="btn btn-primary"
              v-if="cartProducts.length"
              data-dismiss="modal"
              @click="onCheckout"
            >
              Checkout
            </button>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import InputSearch from "@/components/InputSearch.vue";
export default {
  name: "Navigation",
  components: {
    InputSearch
  },
  data() {
    return {
      showModal: false
    };
  },
  computed: {
    isAuth() {
      return this.$store.getters.auth;
    },
    cartProducts() {
      return this.$store.getters.cartProducts;
    },
    total() {
      let total = 0;
      this.cartProducts.forEach(item => {
        total += (item.price - item.discountPrice) * item.quantity;
      });
      return total.toFixed(2);
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("logout");
    },
    onCart() {
      this.showModal = true;
    },
    onClose() {
      this.showModal = false;
    },
    onUpdate(item, qty) {
      this.$store.commit("UPDATE_ON_CART", { item, qty });
    },
    onRemove(item) {
      this.$store.commit("REMOVE_FROM_CART", item);
    },
    onCheckout() {
      this.$router.push({ name: "Checkout" }).catch(err => {
        console.log(err);
      });
    }
  }
};
</script>
