<template>
  <div class="row justify-content-center">
    <div class="col-md-7">
      <div class="card">
        <div class="card-header">Login</div>
        <div class="card-body">
          <alert-error :form="form">
            <template #url v-if="!isVerify">
              <button class="btn btn-link" @click="onResend(verifyMobile)">
                Verify
              </button>
            </template>
          </alert-error>
          <form autocomplete="off" @submit.prevent="onLogin">
            <div class="form-group row">
              <label for="mobile" class="col-md-4 col-form-label text-md-right"
                >Mobile</label
              >
              <div class="col-md-6">
                <input
                  type="tel"
                  id="mobile"
                  name="mobile"
                  class="form-control"
                  autocomplete="mobile"
                  autofocus="autofocus"
                  required="required"
                  v-model.number="form.mobile"
                />
              </div>
            </div>
            <div class="form-group row">
              <label
                for="password"
                class="col-md-4 col-form-label text-md-right"
                >Password</label
              >
              <div class="col-md-6">
                <input
                  type="password"
                  id="password"
                  name="password"
                  class="form-control"
                  required="required"
                  autocomplete="current-password"
                  v-model="form.password"
                />
              </div>
            </div>
            <div class="form-group row mb-0">
              <div class="col-md-6 offset-md-4">
                <button
                  type="submit"
                  class="btn btn-success"
                  :disabled="form.busy"
                >
                  <span v-if="form.busy">
                    <span class="spinner-grow spinner-grow-sm"></span>
                    Login...
                  </span>
                  <span v-else>Login</span>
                </button>
                <router-link
                  :to="{ name: 'Forgot' }"
                  class="btn btn-link text-success"
                  >Forgot Your Password?</router-link
                >
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Form } from "@/lib/form";
export default {
  name: "Login",
  data() {
    return {
      form: new Form({
        mobile: null,
        password: null
      }),
      verifyMobile: null,
      isVerify: true
    };
  },
  methods: {
    onLogin() {
      this.isVerify = true;
      this.verifyMobile = null;
      this.form
        .submit("post", "login")
        .then(() => {
          localStorage.setItem("auth", "true");
          this.$store.commit("AUTH_VALID");
          this.$router.push({ name: "Dashboard" });
        })
        .catch(error => {
          if (error.response.status === 403) {
            this.isVerify = false;
            this.verifyMobile = this.form.mobile;
          }
          console.log(error);
        });
    },
    onResend(mobile) {
      debugger;
      this.$axios
        .post("sms/resend", {
          mobile: mobile
        })
        .then(response => {
          const payload = {
            message: response.data.message,
            mobile: this.form.mobile
          };
          this.$store.commit("AUTH_NOTIFY", payload);
          this.$router.push({ name: "Verify" });
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  mounted() {
    if (!this.$store.getters.token) this.$store.dispatch("csrf");
    this.$store.dispatch("csrf");
  }
};
</script>

<style></style>
