import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import About from "../views/About.vue";
import Contact from "../views/Contact.vue";
import Search from "../views/Search.vue";
import Categories from "../views/Categories.vue";
import Category from "../views/Category.vue";
import Medicine from "../views/Medicine.vue";
import Checkout from "../views/Checkout.vue";
import Track from "../views/Track.vue";
import ReturnPolicy from "../views/ReturnPolicy.vue";
import Register from "../views/auth/Register.vue";
import Verify from "../views/auth/Verify.vue";
import Forgot from "../views/auth/Forgot.vue";
import Reset from "../views/auth/Reset.vue";
import Login from "../views/auth/Login.vue";
import PageNotFound from "../views/PageNotFound.vue";

Vue.use(VueRouter);

const prefixWith = (prefix, routes) =>
  routes.map(route => {
    route.path = prefix + route.path;
    return route;
  });

export default new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    //public routes
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/about-us",
      name: "About",
      component: About
    },
    {
      path: "/contact-us",
      name: "Contact",
      component: Contact
    },
    {
      path: "/search/:query",
      name: "Search",
      component: Search
    },
    {
      path: "/categories/",
      name: "Categories",
      component: Categories
    },

    {
      path: "/category/:slug",
      name: "Category",
      component: Category
    },
    {
      path: "/medicine/:slug",
      name: "Medicine",
      component: Medicine
    },
    {
      path: "/checkout/",
      name: "Checkout",
      component: Checkout
    },
    {
      path: "/track/",
      name: "Track",
      component: Track
    },
    {
      path: "/return-policy/",
      name: "ReturnPolicy",
      component: ReturnPolicy
    },
    //Auth routes
    ...prefixWith("/auth/", [
      {
        path: "register",
        name: "Register",
        component: Register,
        meta: { layout: "Auth" }
      },
      {
        path: "verify",
        name: "Verify",
        component: Verify,
        // beforeEnter: (to, from, next) => {
        //   if (from.name === "Register" || from.name === "Login") next();
        //   else next({ name: "Login" });
        // },
        meta: { layout: "Auth" }
      },
      {
        path: "forgot",
        name: "Forgot",
        component: Forgot,
        meta: { layout: "Auth" }
      },
      {
        path: "reset",
        name: "Reset",
        component: Reset,
        meta: { layout: "Auth" }
      },
      {
        path: "login",
        name: "Login",
        component: Login,
        meta: { layout: "Auth" }
      }
    ]),
    // 404 Route
    {
      path: "/404",
      alias: "*",
      component: PageNotFound
    }
    //private routes
    //
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) return savedPosition;
    else return { x: 0, y: 0 };
  }
});
