<template>
  <div class="w-100">
    <form @submit.prevent="onSearch" autocomplete="off" @keyup="onSearch">
      <div class="search shadow">
        <input
          type="search"
          name="search"
          id="search"
          v-model="query"
          autocomplete="off"
          placeholder="search"
        />
        <button type="submit">
          <i class="fas fa-search"></i>
        </button>
      </div>
    </form>
    <div class="w-100 position-relative" v-if="data.length > 0">
      <ul class="list-unstyled autocomplete bg-light">
        <li class="border-bottom" v-for="(item, index) in data" :key="index">
          <router-link
            :to="{ name: 'Medicine', params: { slug: item.slug } }"
            class="media align-items-center"
            @click.native="onRoute"
          >
            <img
              class="mr-3"
              :src="$storage + item.photos[0]"
              width="50"
              height="50"
              :alt="item.name"
            />
            <div class="media-body">
              <h6 class>{{ item.name }}</h6>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "InputSearch",
  data() {
    return {
      query: "",
      data: []
    };
  },
  computed: {
    medicines() {
      return this.$store.getters.getMedicines;
    }
  },
  methods: {
    onSearch() {
      const word = this.query.trim().toLowerCase();
      if (!word.length) return (this.data = []);
      this.data = this.medicines.filter(medicine => {
        return (
          medicine.slug.toLowerCase().includes(word) ||
          medicine.name.toLowerCase().includes(word)
        );
      });
    },
    onRoute() {
      this.data = [];
    }
  },
  mounted() {
    // if (Object.keys(this.$store.getters.getMedicines).length === 0)
    //   this.$store.dispatch("fetchMedicines");
  }
};
</script>

