import axios from "axios";
import router from "@/router";
import Cookie from "js-cookie";

export default {
  state: {
    isLoggedIn: !!localStorage.getItem("auth"),
    isToken: !!Cookie.get("XSRF-TOKEN"),
    notify: {}
  },
  mutations: {
    AUTH_VALID(state) {
      state.isLoggedIn = true;
    },
    AUTH_LOGOUT(state) {
      state.isLoggedIn = false;
      localStorage.removeItem("auth");
      router.push({ name: "Login" });
    },
    AUTH_TOKEN(state) {
      state.isToken = true;
    },
    AUTH_NOTIFY(state, data) {
      state.notify = data;
    }
  },
  actions: {
    csrf({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .get(process.env.VUE_APP_APIURL + "csrf-cookie")
          .then(response => {
            commit("AUTH_TOKEN");
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    logout({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post("logout")
          .then(response => {
            commit("AUTH_LOGOUT");
            resolve(response);
          })
          .catch(error => {
            commit("AUTH_LOGOUT");
            reject(error);
          });
      });
    }
  },
  getters: {
    auth: state => state.isLoggedIn,
    token: state => state.isToken,
    notify: state => state.notify
  }
};
