<template>
  <div class="row justify-content-center">
    <div class="col-md-8">
      <div class="card">
        <div class="card-header">Create Account</div>
        <div class="card-body">
          <alert-error :form="form"></alert-error>
          <form
            autocomplete="off"
            @submit.prevent="onRegister"
            @keydown="form.onKeydown($event)"
          >
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="firstName">First Name</label>
                <input
                  type="text"
                  id="firstName"
                  name="firstName"
                  v-model="form.firstName"
                  :class="[
                    'form-control',
                    form.errors.has('firstName') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="firstName"></has-error>
              </div>
              <div class="form-group col-md-6">
                <label for="lastName">Last Name</label>
                <input
                  type="text"
                  id="lastName"
                  name="lastName"
                  v-model="form.lastName"
                  :class="[
                    'form-control',
                    form.errors.has('lastName') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="lastName"></has-error>
              </div>
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="email">Email</label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  v-model="form.email"
                  :class="[
                    'form-control',
                    form.errors.has('email') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="email"></has-error>
              </div>
              <div class="form-group col-md-6">
                <label for="mobile">Mobile</label>
                <input
                  type="tel"
                  id="mobile"
                  name="mobile"
                  v-model.number="form.mobile"
                  :class="[
                    'form-control',
                    form.errors.has('mobile') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="mobile"></has-error>
              </div>
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <label>Gender</label>
                <br />
                <div class="custom-control custom-radio custom-control-inline">
                  <input
                    type="radio"
                    id="female"
                    name="gender"
                    value="Female"
                    v-model="form.gender"
                    @change="form.errors.clear('gender')"
                    :class="[
                      'custom-control-input',
                      form.errors.has('gender') ? 'is-invalid' : ''
                    ]"
                  />
                  <label class="custom-control-label" for="female"
                    >Female</label
                  >
                </div>
                <div class="custom-control custom-radio custom-control-inline">
                  <input
                    type="radio"
                    id="male"
                    name="gender"
                    class="custom-control-input"
                    value="Male"
                    v-model="form.gender"
                    @change="form.errors.clear('gender')"
                    :class="[
                      'custom-control-input',
                      form.errors.has('gender') ? 'is-invalid' : ''
                    ]"
                  />
                  <label class="custom-control-label" for="male">Male</label>
                </div>
              </div>

              <div class="form-group col-md-6">
                <label for="dob">Date of Birth</label>
                <input
                  type="text"
                  id="dob"
                  name="dob"
                  v-model="form.dob"
                  :class="[
                    'form-control',
                    form.errors.has('dob') ? 'is-invalid' : ''
                  ]"
                />
                <has-error :form="form" field="dob"></has-error>
              </div>
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="password">Password</label>
                <input
                  type="password"
                  id="password"
                  name="password"
                  v-model="form.password"
                  :class="[
                    'form-control',
                    form.errors.has('password') ? 'is-invalid' : ''
                  ]"
                  autocomplete="new-password"
                  data-initial-value
                />
                <has-error :form="form" field="password"></has-error>
              </div>
              <div class="form-group col-md-6">
                <label for="password-confirmation">Confirm Password</label>
                <input
                  type="password"
                  id="password-confirmation"
                  name="password_confirmation"
                  v-model="form.password_confirmation"
                  :class="[
                    'form-control',
                    form.errors.has('password_confirmation') ? 'is-invalid' : ''
                  ]"
                />
                <has-error
                  :form="form"
                  field="password_confirmation"
                ></has-error>
              </div>
            </div>
            <div class="form-group row mb-0">
              <div class="col-md-6 offset-md-4">
                <button
                  type="submit"
                  class="btn btn-success"
                  :disabled="form.errors.any() || form.busy"
                >
                  <span v-if="form.busy">
                    <span class="spinner-grow spinner-grow-sm"></span>
                    Registering...
                  </span>
                  <span v-else>Register</span>
                </button>
                <router-link
                  :to="{ name: 'Login' }"
                  class="btn btn-link text-success"
                  >Already Registered ?</router-link
                >
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Form } from "@/lib/form";
export default {
  name: "Register",
  data() {
    return {
      form: new Form({
        firstName: "",
        lastName: "",
        email: "",
        mobile: null,
        gender: "",
        dob: "",
        password: "",
        password_confirmation: ""
      })
    };
  },
  methods: {
    onRegister() {
      this.form
        .submit("post", "register")
        .then(response => {
          this.$store.commit("AUTH_NOTIFY", response.data.message);
          this.$router.push({ name: "Verify" });
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  mounted() {
    if (!this.$store.getters.token) this.$store.dispatch("csrf");
  }
};
</script>

<style></style>
